import snapshot from './Snapshot.json'
import jsonfile from 'jsonfile'
const writejson = require('write');
const xrpl = require("xrpl")

class Ledger {
    constructor() {
        this.PUBLIC_SERVER = "wss://xrplcluster.com/";
        this.client = new xrpl.Client(this.PUBLIC_SERVER);
        this.wallet = xrpl.Wallet.fromSeed(process.env.REACT_APP_WALLET_DISTRIBUTER_SEED);
        this.issuerToken = process.env.REACT_APP_WALLET_ISSUER_TOKEN;
        this.issuerAddress = process.env.REACT_APP_WALLET_ISSUER_ADDRESS;
        this.isConnected = false;
        this.errorOccured = false;
        this.errorMessage = "";
        this.request = {};
        this.xrpAddress = "";

    }

    distributePayment = async() => {
        await this.connect();

        if (this.isConnected) {
            console.log('Connected to the server!');

            if (process.env.REACT_APP_WALLET_DISTRIBUTER_SEED && snapshot && snapshot.length > 0) {
                this.wallet = xrpl.Wallet.fromSeed(process.env.REACT_APP_WALLET_DISTRIBUTER_SEED);

                const result = [];

                for (let i = 0; i < snapshot.length; i++) {
                    const obj = snapshot[i];
                    const [stat, ret] = await this.checkAddressStatus(obj.Address);
                    let done = false;
                    if (ret) {
                        await this.sendXMWToken(obj.Address, process.env.REACT_APP_WALLET_ISSUER_ADDRESS, 
                            process.env.REACT_APP_WALLET_ISSUER_TOKEN, process.env.REACT_APP_WALLET_RECEIVER_ADDRESS)
                            .then(res => {
                                obj.Message = res;
                                obj.Status = stat;
                            })
                        
                            done= true;
                    } else {
                        obj.Status = stat;
                        obj.Message = stat;
                        done= true;
                    }

                    if (done) {
                        console.log(JSON.stringify(obj));
                        result.push(obj);
                    }
                }


                if (result.length > 0) {
                    console.log(JSON.stringify(result));
                }


                
                //this.getAccountObjects("rwGW8L6ftKx6RPPfLqNJQyFP1haa8WtSgH");

                //const addresses = ['rUHANRAVMVCiYb6Sn7KdmhYGhAd9iEAWVz'];
                
                // addresses.map(async(address) => {
                //     await this.setTrustLine(address, process.env.REACT_APP_WALLET_ISSUER_ADDRESS, 
                //         process.env.REACT_APP_WALLET_ISSUER_TOKEN, process.env.REACT_APP_WALLET_RECEIVER_ADDRESS); //SUCCESS
                // });

                //this.sendPayment(process.env.REACT_APP_WALLET_RECEIVER_ADDRESS);

                
                //this.setTrustLine();

                //  const bal = await this.getBalances(process.env.REACT_APP_WALLET_RECEIVER_ADDRESS); //SUCCESS
                //  console.log(bal.length)

                // this.getLedgerData(); SUCCESS
            } else {
                this.errorOccured = true;
                this.errorMessage = "Distributer credentials is not valid!"
            }
        } else {
            this.errorOccured = true;
            this.errorMessage = "Failed to connect to the server!"
        }
    }

    /**
     * Connect the initialized client to the server
     * @returns Promise<void>
     * @returns A promise that resolves with a void value when a connection is established.
     */
    connect = async() => {
        await this.client.connect().then(() => {
            this.isConnected = this.isClientConnected();
        });
    }

    /**
     * Checks the initialized client connection to the server
     * @returns boolean
     */
    isClientConnected = () => {
        return this.client.isConnected();
    }

    checkAddressStatus = async (address) => {

        if (this.isValidAddress(address)) {

            //await this.getAccountInfo(address);

            return ["VALID", true];
        }


        return ["INVALID", false];
    }

    isValidAddress = (address) => {
        return xrpl.isValidClassicAddress(address);
    }

    /**
     * Fetch the XRP balance of the specified address
     * @param {string} address 
     * @returns Promise<string>
     * @returns The XRP balance of the account (as a string).
     */
    getXRPAddressBalance = async(address) => {
        const balance = await this.client.getXrpBalance(address);
        return balance;
    }

    /**
     * Fetch the balances of the specified address
     * @param {string} address 
     * @returns Promise<{ currency: string; issuer?: string; value: string }[]>
     * @returns An array of XRP/non-XRP balances for the given account.
     */
    getBalances = async(address) => {
        if (this.errorOccured) {
            console.log('Error : ' + this.errorMessage);
            return;
        }

        let xAddress = this.clientAddress;

        if (address) {
            xAddress = address;
        }

        const bal = await this.client.getBalances(xAddress);
        return bal;
    }

    /**
     * Disconnect the initialized client to the server
     * @returns Promise<void>
     * @returns A promise that resolves with a void value when a connection is destroyed.
     */
    disconnect = () => {
        this.client.disconnect();
    }

    sendXMWToken = async(destinationAddress, issuerAddress, issuerToken, amount = 0) => {
        return new Promise(async (resolve, reject) => {
            const prepared = await this.client.autofill(
                {
                    "TransactionType": "Payment",
                    "Account": this.wallet.address,
                    "Amount": {
                        "currency": issuerToken,
                        "value": amount.toString(),
                        "issuer": issuerAddress
                    },
                    "Destination": destinationAddress
                }
            )
            const signed = this.wallet.sign(prepared)
            const tx = await this.client.submitAndWait(signed.tx_blob).catch(err => {
                console.log('Error', destinationAddress , err.stack || err.message);
                resolve("XrplError" || err.message);
            });

            resolve(tx.result.meta.TransactionResult);
        })
    }

    sendPayment = async(amount, address) => {
        let resultMessage = "Cannot connect to the XPRL server.";
       if (!this.isConnected) {
        await this.connect();
       }

       if (this.isConnected) {
        if (this.isValidAddress(address)) {
            await this.sendXMWToken(address, this.issuerAddress, this.issuerToken, amount)
                .then(res => {
                    resultMessage = res;
                });
        } else {
            resultMessage = "XRP Address is Invalid"
        }
        }

       return resultMessage;
    }

    /////////////////////////////////CORE API/////////////////////////////////////////
    /**
     * Autofills fields in a transaction. 
     * This will set Sequence, Fee, lastLedgerSequence 
     * according to the current state of the server this Client is connected to. 
     * It also converts all X-Addresses to classic addresses and flags interfaces into numbers.
     * @param {TRANSACTION in JSON format} transaction 
     * @param {int} signersCount 
     * @returns Promise<T>
     */
    autoFill = (transaction, signersCount) => {
        const response = this.client.autofill(transaction);
        return response;
    }

    submitTransaction = async(transaction) => {
        const response = await this.client.submitAndWait(transaction);

        return response;
    }

    getAccountChannel = async(address) => {
        await this.connect();

        const response = await this.client.requestAll({
            "account": address,
            "command": "account_channels"
        });

        console.log('response', response)
    }

    getAccountLines = async (address) => {
        await this.connect();

        const response = await this.client.requestAll({
            "account": address,
            "command": "account_lines"
        });

        console.log(response)

        return response;
    }

    getAccountInfo = async(address) => {
        const response = await this.client.request({
            "account": address,
            "command": "account_info"
        })

        console.log('response', response);
    }

    getAccountCurrencies = async(address) => {
        await this.connect();

        const response = await this.client.request({
            "account": address,
            "command": "account_currencies"
        })

        //console.log(response)

        return response;
    };

    getAccountObjects = async(address) => {
        const response = await this.client.requestAll({
            "account": address,
            "command": "account_objects"
        });

        console.log('response', response)
    }

    getLedgerData = async() => {
        const response = await this.client.request({
            "command": "ledger_data"
        })

        console.log('response', response);
    }

    getLedger = async() => {
        const response = await this.client.request({
            "command": "ledger"
        })

        console.log('response', response);
    }
}

export default Ledger;